<template>
  <div class="wrap">
    <div class="content">
      <router-view />
    </div>
    <div class="tabbars">
      <div
        :class="['tabbar', { 'tabalive' : tab_alive == item.id }]"
        v-for="item in tabs"
        :key="item.id"
        @click="changetab(item.id)"
      >
        <img :src="tab_alive == item.id ? item.icon : item.icon_o" :alt="item.title" />
        {{ item.title }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
 
  data() {
    return {
      tab_alive: null,
      tabs: [
        {
          id: 1,
          title: "首页",
          icon: require("@/assets/homepages/home.png"),
          icon_o: require("@/assets/homepages/home_o.png")
        },
        {
          id: 2,
          title: "分类",
          icon: require("@/assets/homepages/classify.png"),
          icon_o: require("@/assets/homepages/classify_o.png")
        },
        {
          id: 3,
          title: "购物车",
          icon: require("@/assets/homepages/shopcar.png"),
          icon_o: require("@/assets/homepages/shopcar_o.png")
        },
        {
          id: 4,
          title: "我的",
          icon: require("@/assets/homepages/personal.png"),
          icon_o: require("@/assets/homepages/personal_o.png")
        }
      ]
    };
  },
  created() {
    this.watchid();
  },
  methods: {
    changetab(id) {
      this.watchid(id);
      switch (id) {
        case 1:
          this.$router.push({
            name: "homepages"
          });
          break;
        case 2:
          this.$router.push({
            name: "classify"
          });
          break;
        case 3:
          this.$router.push({
            name: "shopcar"
          });
          break;
        case 4:
          this.$router.push({
            name: "personal"
          });
          break;
      }
    },
    watchid(id = null) {
      switch (this.$route.name) {
        case "homepages":
          id = 1;
          break;
        case "classify":
          id = 2;
          break;
        case "shopcar":
          id = 3;
          break;
        case "personal":
          id = 4;
          break;
      }
      this.tab_alive = id;
    }
  },
  watch: {
    $route() {
      this.watchid();
    }
  }
};
</script>

<style lang="scss" scoped>
.wrap {
  position: relative;
  .content {
    margin-bottom: 98px;
  }
  .tabbars {
    position: fixed;
    z-index: 1000;
    bottom: 0;
    height: 98px;
    width: 100%;
    display: flex;
    justify-content: space-around;
    background-color: #fff;
    color: #999;
    border-top: thin solid #e6e6e6;
    .tabbar {
      font-size: 22px;
      text-align: center;
      padding-top: 10px;

      img {
        margin: 0 auto;
        display: block;
        height: 48px;
        width: 48px;
        // vertical-align: middle;
      }
    }
    .tabalive {
      color: #fd656d;
    }
  }
}
</style>




